    padding: var(--spacing-lg) var(--spacing-md);
  }
  .footer {
    padding-top: var(--spacing-2xl);
    padding-bottom: var(--spacing-xl);
  }
  .footer-container {
    padding-left: var(--spacing-lg);
    padding-right: var(--spacing-lg);
  }
  .footer-content {
    gap: var(--spacing-2xl);
    flex-direction: column;
    padding-bottom: var(--spacing-2xl);
  }
  .footer-bottom {
    padding-top: var(--spacing-lg);
  }
  .footer-column-brand {
    flex: 1;
  }
  .footer-column-connect {
    flex: 1;
  }
  .footer-column-legal {
    flex: 1;
  }
  .footer-social-grid {
    gap: var(--spacing-sm);
    grid-template-columns: repeat(4, 1fr);
  }
  .footer-social-link {
    padding: var(--spacing-sm);
  }
  .footer-social-label {
    font-size: 0.65rem;
  }
  .hero-section {
    min-height: 100vh;
  }
  .hero-wrapper {
    padding: var(--spacing-2xl) var(--spacing-md);
  }
  .hero-profile-container {
    width: 180px;
    height: 180px;
  }
  .home-hero-subtitle {
    font-size: var(--font-size-lg);
  }
  .features-section {
    padding: var(--spacing-3xl) 0;
  }
  .features-bento-grid {
    gap: var(--spacing-sm);
    grid-template-columns: repeat(2, 1fr);
  }
  .features-social-card {
    padding: var(--spacing-lg);
    min-height: 140px;
  }
  .features-card-large {
    min-height: 180px;
    grid-column: span 2;
  }
  .features-icon {
    width: 36px;
    height: 36px;
  }
  .features-card-label {
    font-size: var(--font-size-base);
  }
  .cta-section {
    padding: var(--spacing-3xl) 0;
  }
  .cta-wrapper {
    padding: 0 var(--spacing-md);
  }
  .cta-content {
    gap: var(--spacing-xl);
  }
  .cta-form-group {
    flex-direction: column;
  }
  .cta-submit {
    justify-content: center;
  }
  .about-section {
    padding: var(--spacing-3xl) 0;
  }
  .about-wrapper {
    padding: 0 var(--spacing-md);
  }
  .about-split-container {
    gap: var(--spacing-lg);
  }
  .showcase-section {
    padding: var(--spacing-3xl) 0;
  }
  .showcase-wrapper {
    padding: 0 var(--spacing-md);
  }
  .showcase-item {
    gap: var(--spacing-xl);
    padding: var(--spacing-xl);
  }
}
 
@media(max-width: 479px) {
  .thq-section-padding {
    padding: var(--dl-layout-space-oneandhalfunits);
  }
  .thq-flex-column {
    gap: var(--dl-layout-space-unit);
  }
  .thq-flex-row {
    gap: var(--dl-layout-space-unit);
  }
  .thq-grid-6 {
    grid-gap: var(--dl-layout-space-unit);
    grid-template-columns: 1fr 1fr;
  }
  .thq-grid-5 {
    grid-gap: var(--dl-layout-space-unit);
    grid-template-columns: 1fr 1fr;
  }
  .thq-grid-3 {
    grid-gap: var(--dl-layout-space-unit);
    align-items: center;
    grid-template-columns: 1fr;
  }
  .thq-grid-4 {
    grid-gap: var(--dl-layout-space-unit);
    align-items: center;
    flex-direction: column;
    grid-template-columns: 1fr;
  }
  .thq-grid-2 {
    grid-gap: var(--dl-layout-space-unit);
  }
  .thq-grid-auto-300 {
    grid-template-columns: 1fr;
  }
  .navigation-mobile-header {
    padding: var(--spacing-sm) var(--spacing-md);
  }
  .navigation-mobile-links {
    padding: var(--spacing-md);
  }
  .navigation-mobile-link {
    padding: var(--spacing-sm) var(--spacing-md);
    font-size: var(--font-size-base);
  }
  .footer-social-grid {
    grid-template-columns: repeat(4, 1fr);
  }
  .footer-social-link {
    padding: var(--spacing-xs);
  }
  .footer-social-label {
    font-size: 0.6rem;
  }
  .hero-profile-container {
    width: 160px;
    height: 160px;
  }
  .features-bento-grid {
    grid-template-columns: 1fr;
  }
  .features-card-large {
    grid-column: span 1;
  }
}
